@font-face {
  font-family: "ONE-Mobile-POP";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-POP.woff")
    format("woff");
  font-weight: 200;
  font-style: normal;
}
@font-face {
  font-family: "SUIT-Regular";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_suit@1.0/SUIT-Regular.woff2")
    format("woff2");
  font-weight: normal;
  font-style: normal;
}

:root {
  --primary: #372f2a;
  --primary-light: #574b45;
  --primary-dark: #130f0d;
  --secondary: #4a7053;
  --point-color: #ffd912;
  --BE-Box: rgba(192, 177, 143, 0.6);
  --BK-Box: rgba(0, 0, 0, 0.85);
}

*,
html,
body {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
html {
  font-family: "ONE-Mobile-POP", sans-serif;
  font-size: 10px;
  color: var(--primary);
  line-height: 1.6;
}
body {
  position: relative;
  font-size: 1.6rem;
}
input:focus {
  outline: none;
}
a {
  text-decoration: none;
  display: block;
  color: var(--primary);
}
ul,
ol {
  list-style: none;
}
div,
content,
header {
  box-sizing: border-box;
}
button {
  position: relative;
  cursor: pointer;
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-family: "ONE-Mobile-POP";
  font-size: 1.6rem;
  background-color: var(--primary);
}
button:active,
a,
label {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
button:disabled {
  opacity: 0.3;
  background-color: var(--BE-Box);
  color: var(--primary);
}
h1,
h2,
h3,
h4 {
  font-weight: normal;
}

div.wrap {
  width: 100%;
  position: relative;
  background: url(../img/bg.jpg);
  z-index: 1;
  float: left;
}

content {
  float: left;
  width: 100%;
  /* z-index: 3; */
  position: relative;
  text-align: center;
}
.caption {
  font-family: "SUIT-Regular";
  font-size: 1.4rem;
  line-height: 1.4;
  opacity: 0.6;
}
.tooltip {
  display: none;
  background: #333;
  padding: 5px;
  font-family: "SUIT-Regular";
  font-size: 1.4rem;
  line-height: 1.4;
}
.tooltip:hover {
  display: block;
}

body::-webkit-scrollbar {
  width: 12px;
}
body::-webkit-scrollbar-thumb {
  background-color: var(--primary-light);
}
body::-webkit-scrollbar-track {
  background-color: var(--BE-Box);
}

.zoomin {
  animation: zoomin 0.3s ease-in;
}
@keyframes zoomin {
  0% {
    transform: scale(0, 0);
  }
  70% {
    transform: scale(1.2, 1.2);
  }
  100% {
    transform: scale(1, 1);
  }
}

input[type*="checkbox"] {
  display: none;
}

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 101;
}
input:checked ~ .popup {
  display: block;
}
input ~ .popup > div {
  z-index: 102;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 90%;
  background: var(--BK-Box);
  border-radius: 8px;
  color: #fff;
  padding: 40px;
}

div.text {
  overflow: auto;
  width: 100%;
  height: 100%;
  padding-right: 10px;
  box-sizing: border-box;
}

/* scrollbar */
div.text::-webkit-scrollbar {
  width: 10px;
}
div.text::-webkit-scrollbar-thumb {
  background-color: var(--BE-Box);
}
div.text::-webkit-scrollbar-track {
  background-color: var(--primary);
}

/* close */
label.close {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
  width: 54px;
  height: 54px;
  background: var(--primary);
  border-radius: 100%;
  z-index: 1;
}
label.close::before,
label.close::after {
  position: absolute;
  left: 24px;
  top: 12px;
  content: " ";
  height: 30px;
  width: 4px;
  background-color: #fff;
  border-radius: 50px;
}
label.close::before {
  transform: rotate(45deg);
}
label.close::after {
  transform: rotate(-45deg);
}

/* background */
div.popup > label {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}

.popup .text h3 {
  margin-bottom: 5px;
  font-size: 2.4rem;
  text-align: center;
}

@media screen and (max-width: 1023px) {
  div.wrap {
    min-width: 280px;
  }

  input ~ .popup > div {
    width: 92%;
    height: 80%;
    padding: 15px;
  }
  label.close {
    top: -6%;
    right: -2%;
    transform: translate(0, 0);
  }
}
