#index {
  height: 100%;
}

#nowallet {
  margin-top: 100px;
}

div.wrap {
  height: 100%;
  overflow: hidden;
}
span.backImg {
  /* z-index: 2; */
  position: absolute;
  bottom: -16px;
  width: 24%;
  max-width: 295px;
  animation: zoomin ease-out 0.4s forwards;
  animation-delay: 0.8s;
  transform: scale(0, 0);
}
span.left {
  left: 0;
}
span.right {
  right: 0;
}
span.backImg > img {
  width: 100%;
  position: relative;
}
span.left img {
  animation: leftright ease-in-out 1s infinite alternate;
}
@keyframes leftright {
  0% {
    left: -8%;
  }
  100% {
    left: -12%;
  }
}
span.right img {
  animation: rightleft ease-in-out 1s infinite alternate;
}
@keyframes rightleft {
  0% {
    right: -8%;
  }
  100% {
    right: -12%;
  }
}

#index content {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main_header {
  width: 100%;
  padding-top: 5vh;
  text-align: center;
  position: relative;
  top: -99em;
  animation: topbottom ease-out 0.4s forwards;
}
@keyframes topbottom {
  0% {
    top: -99em;
  }
  100% {
    top: 0;
  }
}
.main_header img {
  width: 30%;
  max-width: 400px;
  min-width: 220px;
  animation: swing ease-in-out 1s infinite alternate;
}
@keyframes swing {
  0% {
    transform: rotate(3deg);
  }
  100% {
    transform: rotate(-3deg);
  }
}

nav#menu {
  margin: 0 auto;
  margin-top: 6.5vh;
}
nav#menu ul {
  display: inline-block;
  width: auto;
  text-align: left;
}
nav#menu ul li a,
nav#menu label {
  font-size: 3.2rem;
  cursor: pointer;
}
.triangle {
  opacity: 0;
  position: relative;
  top: -10px;
  margin-right: 15px;
  font-size: 0;
  width: 0px;
  height: 0px;
  border-left: 20px solid var(--secondary);
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  transition: all 0.3s;
}
nav#menu li:hover .triangle {
  opacity: 1;
}

#index input + label {
  font-size: 3.2rem;
  cursor: pointer;
}

#footer {
  font-family: "SUIT-Regular";
  font-weight: 600;
  margin-top: auto;
  padding-bottom: 3vh;
}
#footer p a {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
#footer p img {
  width: 16px;
  margin-right: 6px;
}
#footer #copyright {
  font-size: 1.4rem;
}

@media screen and (max-width: 1023px) {
  span.backImg {
    width: 40%;
    bottom: -2%;
  }

  .main_header {
    padding-top: 12%;
  }
  .main_header img {
    width: 220px;
  }

  nav#menu ul li a,
  nav#menu label {
    font-size: 2rem;
    line-height: 1.8;
  }

  .triangle {
    top: -8px;
    margin-right: 12px;
    border-left: 22px solid var(--secondary);
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;
  }

  #footer {
    padding-bottom: 2vh;
  }
  #footer p a {
    font-size: 1.4rem;
  }
  #footer p img {
    width: 14px;
    margin-right: 6px;
  }
  #footer #copyright {
    font-size: 1.2rem;
  }
}
