#ranking {
  height: 100%;
}

#ranking .wrap {
  text-align: center;
  min-height: 100%;
  overflow: auto;
}
.bgmove {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: url(../img/bg.jpg); */
  /* animation: bgmove 4s linear infinite alternate-reverse; */
}
@keyframes bgmove {
  0% {
    left: -20%;
    top: -20%;
  }
  100% {
    left: -15%;
    top: -15%;
  }
}

.ranking_header {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 4;
  background: var(--primary);
}
.ranking_header > div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 30px;
}
.ranking_header a.logo {
  height: 40px;
  padding: 0;
  float: left;
}
.ranking_header a.logo img {
  width: auto;
  height: 100%;
}

.ranking_header a.play_btn {
  height: 30px;
  font-size: 2rem;
  color: var(--point-color);
}
.ranking_header a.play_btn img {
  width: auto;
  height: 100%;
}

input[id*="header_btn"] + .ranking_header > div > label {
  float: right;
  display: block !important;
  width: 30px;
  height: 30px;
  position: relative;
  margin-top: 6px;
  transition: 0.1s;
}
input[id*="header_btn"] + .ranking_header > div > label span {
  width: 12px;
  height: 12px;
  background-color: var(--point-color);
  display: block;
  border-radius: 2px;
  position: absolute;
}
input[id*="header_btn"] + .ranking_header > div > label:hover span {
  transform: scale(1.1);
  transition: 350ms cubic-bezier(0.8, 0.5, 0.2, 1.4);
}
input[id*="header_btn"] + .ranking_header > div > label span:nth-child(1) {
  left: 0;
  top: 0;
}
input[id*="header_btn"] + .ranking_header > div > label span:nth-child(2) {
  right: 0;
  top: 0;
}
input[id*="header_btn"] + .ranking_header > div > label span:nth-child(3) {
  left: 0;
  bottom: 0;
}
input[id*="header_btn"] + .ranking_header > div > label span:nth-child(4) {
  right: 0;
  bottom: 0;
}
input[id*="header_btn"]:checked + .ranking_header > div > label {
  transform: rotate(180deg);
  cursor: pointer;
  transition: 0.2s cubic-bezier(0.8, 0.5, 0.2, 1.4);
}
input[id*="header_btn"]:checked + .ranking_header > div > label span {
  width: 20px;
  height: 6px;
  transition-delay: 200ms;
  transition: 0.5s cubic-bezier(0.8, 0.5, 0.2, 1.4);
}
input[id*="header_btn"]:checked
  + .ranking_header
  > div
  > label
  span:nth-child(1) {
  top: 6px;
  transform: rotate(45deg);
}
input[id*="header_btn"]:checked
  + .ranking_header
  > div
  > label
  span:nth-child(2) {
  top: 6px;
  transform: rotate(-45deg);
}
input[id*="header_btn"]:checked
  + .ranking_header
  > div
  > label
  span:nth-child(3) {
  bottom: 6px;
  transform: rotate(-45deg);
}
input[id*="header_btn"]:checked
  + .ranking_header
  > div
  > label
  span:nth-child(4) {
  bottom: 6px;
  transform: rotate(45deg);
}

.ranking_content {
  padding-top: 110px;
  padding-bottom: 50px;
}
.ranking_content > img {
  width: 70px;
  height: 70px;
}

#team_points {
  width: 90%;
  max-width: 1500px;
  margin: 0 auto;
  padding-left: 6px;
  padding-right: 6px;
  margin-bottom: 60px;
}
#team_points ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: stretch;
  flex-wrap: wrap;
  align-content: stretch;
}
#team_points li {
  width: 33.3%;
  min-width: 25%;
  text-align: left;
}
#team_points li > div {
  margin: 0 5px 10px 5px;
  padding: 20px 30px;
  background: rgba(223, 198, 157, 0.85);
  border-radius: 20px;
  box-shadow: 0 2px 4px rgba(197, 177, 143, 0.9);
  animation: zoomin 0.3s ease-in;
}
.team_points_team {
  display: inline-block;
  width: 25px;
  height: 25px;
  margin-right: 10px;
  background-color: var(--primary);
  border-radius: 50px;
  font-size: 1.2rem;
  line-height: 25px;
  font-family: "SUIT-Regular";
  font-weight: 600;
  color: #fff;
  text-align: center;
}
.team_points_point {
  float: right;
  text-align: right;
}

h3 {
  line-height: 1;
  margin-top: -5px;
  margin-bottom: 40px;
  font-size: 3.2rem;
  color: var(--primary);
}
h4 {
  margin-bottom: 15px;
  font-size: 2.4rem;
  color: var(--primary);
}
table {
  border-collapse: collapse;
}
tr {
  display: table;
  width: 100%;
  margin-bottom: 8px;
  background: rgba(223, 198, 157, 0.85);
  border-collapse: collapse;
  animation: zoomin 0.3s ease-in;
  border-radius: 50px;
  box-shadow: 0 2px 4px rgba(197, 177, 143, 0.9);
}
thead tr {
  background: rgba(194, 165, 117, 1);
}
tbody tr:hover {
  box-shadow: 0 0 10px #eddaba, 0 0 20px #eddaba, 0 0 30px #eddaba;
}

tr.rank_1st {
  /* background: #F3E07C; */
  background: #f5cb36;
}
tr.rank_1st:hover {
  box-shadow: 0 0 10px #f3e07c, 0 0 20px #f3e07c, 0 0 30px #f3e07c;
}
tr.rank_2nd {
  /* background: #E9E9E9; */
  background: #c2c2c7;
}
tr.rank_2nd:hover {
  box-shadow: 0 0 10px #d2d2d7, 0 0 20px #d2d2d7, 0 0 30px #d2d2d7;
}
tr.rank_3rd {
  /* background: #E0C09B; */
  background: #d0a552;
}
tr.rank_3rd:hover {
  box-shadow: 0 0 10px #dfb86d, 0 0 20px #dfb86d, 0 0 30px #dfb86d;
}

th {
  padding: 5px 2px;
  font-weight: normal;
}
td {
  text-align: center;
  font-size: 1.6rem;
  font-family: "SUIT-Regular";
  padding: 5px 2px;
  font-weight: 600;
}
td label {
  display: block;
  cursor: pointer;
}
th:first-child,
td:first-child {
  border-radius: 50px 0 0 50px;
  font-family: "ONE-Mobile-POP";
  width: 16%;
  font-weight: 200;
}

th:nth-child(2),
td:nth-child(2) {
  width: 58%;
}
th:nth-child(3),
td:nth-child(3) {
  width: 6%;
}
th:last-child,
td:last-child {
  border-radius: 0 50px 50px 0;
  width: 20%;
}
td:nth-child(3) span {
  display: inline-block;
  width: 25px;
  height: 25px;
  font-size: 1.2rem;
  line-height: 25px;
  font-family: "SUIT-Regular";
  background-color: var(--primary);
  border-radius: 50px;
  color: #fff;
}

#rankings_div {
  overflow-y: auto;
  margin: 0 auto;
  width: 90%;
  max-width: 1500px;
  margin-bottom: 60px;
  padding-left: 16px;
}
#rankings_div table {
  width: calc(100% - 10px);
}
#rankings_div thead {
  float: left;
  width: calc(100% - 16px);
}
#rankings_div tbody {
  width: 100%;
  padding-right: 10px;
  float: left;
  display: block;
  height: 224px;
  overflow-y: scroll;
  box-sizing: border-box;
}
#paging {
  margin-right: 10px;
  margin-top: 15px;
  padding: 10px 20px;
  display: inline-block;
  background: none;
  border: 1px solid var(--primary);
  color: var(--primary);
  transition: all 0.3s;
}
#paging:hover {
  background: var(--primary);
  color: #fff;
}
#rankings_div tbody::-webkit-scrollbar {
  width: 6px;
}
#rankings_div tbody::-webkit-scrollbar-thumb {
  background-color: var(--primary-light);
}
#rankings_div tbody::-webkit-scrollbar-track {
  background-color: var(--BE-Box);
}

#my_ranking_wrap {
  margin: 0 auto;
  width: 90%;
  max-width: 1500px;
  margin-bottom: 50px;
  padding-left: 16px;
}
#my_ranking_wrap table {
  width: calc(100% - 16px);
}
#my_ranking_wrap td {
  color: #857150;
}
#my_ranking {
  background: rgba(194, 165, 117, 1);
  position: relative;
}
#my_ranking td {
  color: var(--primary);
}

#my_ranking_wrap span.my_icon {
  position: absolute;
  left: 6px;
  top: 50%;
  transform: translateY(-50%);
  padding: 2px 4px;
  border-radius: 50px;
  background-color: rgba(241, 221, 188, 1);
  font-family: "SUIT-Regular";
  font-weight: 600;
  font-size: 1.2rem;
}

@media screen and (max-width: 1023px) {
  .ranking_header > div {
    padding: 10px 15px;
  }
  .ranking_content > img {
    width: 60px;
    height: 60px;
  }
  .ranking_header a.play_btn {
    display: none;
  }

  .ranking_content {
    padding-top: 60px;
  }

  h3 {
    font-size: 2.8rem;
    margin-bottom: 20px;
  }
  h4 {
    font-size: 2rem;
  }
  #team_points {
    width: 100%;
    padding-left: 16px;
    padding-right: 16px;
  }
  #team_points ul {
    flex-direction: column;
  }
  #team_points li {
    width: 100%;
    text-align: left;
    flex: 1;
    min-width: 20%;
    margin: 0 0 8px 0;
    padding: 0;
  }
  #team_points li > div {
    margin: 0;
    padding: 6px 15px 6px 10px;
    background: rgba(223, 198, 157, 0.85);
    border-radius: 20px;
    box-shadow: 0 2px 4px rgba(197, 177, 143, 0.9);
    animation: zoomin 0.3s ease-in;
  }
  #team_points .team_points_point {
    font-size: 1.4rem;
  }
  #rankings_div {
    width: 100%;
  }
  #rankings_div tbody {
    height: 215px;
  }
  #my_ranking_wrap {
    width: 100%;
  }
  #my_ranking_wrap span.my_icon {
    display: none;
    /* left:-1px;
    background-color: rgba(194,165,117,1); */
  }
  th {
    font-size: 1.2rem;
  }
  td {
    font-size: 1.4rem;
  }
  th:first-child,
  td:first-child {
    width: 20%;
  }

  th:nth-child(2),
  td:nth-child(2) {
    width: auto;
  }
  th:nth-child(3),
  td:nth-child(3) {
    width: 14%;
  }
  th:last-child,
  td:last-child {
    width: 28%;
  }
}
