#discord_id_check {
  z-index: 102;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: auto;
  height: auto;
  background: rgba(192, 177, 143, 1);
  border-radius: 8px;
  color: var(--primary);
  padding: 25px 100px;
  text-align: center;
}
#discord_id_check img {
  width: 120px;
}
#discord_id_check p {
}
#discord_id_check #discord_id_check_tit {
  font-size: 1.6rem;
}
#discord_id_check #discord_id_check_ID {
  font-size: 2.6rem;
  font-weight: 600;
  margin-top: -6px;
  margin-bottom: 25px;
}
#discord_id_check .discord_id_check_dsc {
  font-size: 1.8rem;
}
#discord_id_check .discord_id_check_address {
  font-family: "SUIT-Regular";
  font-weight: 600;
  font-size: 1.8rem;
  margin-top: -2px;
  margin-bottom: 20px;
}
#discord_id_check #discord_id_check_btn {
  margin-top: 30px;
}
#discord_id_check #discord_id_check_btn button {
  padding: 8px 18px;
  margin: 0 5px;
  transition: all 0.4s;
}
#discord_id_check #discord_id_check_btn button[name="cancle"] {
  background: none;
  color: var(--primary-light);
}
#discord_id_check #discord_id_check_btn button[name="cancle"]:hover {
  background: var(--primary-light);
  color: #fff;
}
#discord_id_check #discord_id_check_btn button[name="ok"]:hover {
  background: var(--primary-light);
}

@media screen and (max-width: 1023px) {
  #discord_id_check {
    width: 90%;
    height: auto;
    padding: 25px 10px;
  }

  #discord_id_check #discord_id_check_tit {
    font-size: 1.4rem;
  }
  #discord_id_check #discord_id_check_ID {
    font-size: 2rem;
    font-weight: 600;
    margin-top: -6px;
    margin-bottom: 25px;
  }
  #discord_id_check .discord_id_check_dsc {
    font-size: 1.4rem;
  }
  #discord_id_check .discord_id_check_address {
    font-family: "SUIT-Regular";
    font-weight: 600;
    font-size: 1.4rem;
    margin-top: -2px;
    margin-bottom: 20px;
  }
}
