#instruction h3 {
  color: var(--point-color);
}

#instruction > li,
#instruction > p,
#instruction > span {
  text-align: left;
  font-size: 1.8rem;
  padding-left: 4px;
  font-family: "SUIT-Regular";
}

#manual,
#rules_1,
#rules_2,
#rules_3 {
  width: 100% !important;
  float: left;
  animation: zoomin 0.3s ease-in;
  text-align: left;
}
.instruction_contents > li,
.instruction_contents > p,
.instruction_contents > span {
  text-align: left;
  font-size: 1.8rem;
  padding-left: 4px;
  font-family: "SUIT-Regular";
}

ol#manual {
  margin-bottom: 50px;
  list-style: square;
  list-style-position: outside;
}
ol#manual li {
  margin-left: 24px;
}
#rules_1 li {
  float: left;
  width: 15%;
}
#rules_2 li {
  float: left;
  width: 15%;
}
#rules_2 li span,
#rules_3 p span {
  font-size: 2.6rem;
}
#rules_3 p {
  margin: 15px 0;
  line-height: 1.2;
}

#instruction .pointcausion {
  font-size: 2rem;
  color: red;
}
@media screen and (max-width: 1023px) {
  #rules_1 li {
    float: left;
    width: 20%;
  }
  #rules_2 li {
    width: 30%;
  }
}
