#play {
  height: 100%;
}
.wrap {
  display: flex;
  min-height: 100%;
}
.play_content {
  display: flex;
  flex-direction: row;
  min-height: 100%;
}

#countdown {
  position: absolute;
  left: 42%;
  top: 80%;
  z-index: 100;
  padding: 10px;
  margin: 10px;
  display: block;
  color: white;
  font-size: 10rem;
  text-align: center;
}

/* display block none */
#msg_box_ready {
  display: block;
} /* start버튼 누르면 waiting 문구 */
#msg_box_started {
  display: none;
} /* Started 문구 */
#msg_box_rps {
  display: none;
} /* 가위바위보 문구 */
#msg_box_count {
  display: none;
} /* 하나빼기 321 카운트 문구 */
#msg_box_result {
  display: none;
} /* 승패 문구 */

.msg_box {
  position: fixed;
  top: 50%;
  left: 300px;
  right: 0;
  transform: translateY(-50%);
  background: var(--BK-Box);
  height: 200px;
  z-index: 100;
  color: #fff;
}
.msg_box > div {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.msg_box h3 {
  color: #fff;
  font-size: 4rem;
}
.msg_box h2 {
  font-size: 6rem;
  line-height: 1.2;
}
.msg_box p {
  font-size: 3rem;
}

/* 글자 나타나기 */
.msg_box h3,
.msg_box h2 {
  transform: translateX(-300px);
  animation: txt1 0.3s forwards;
}
@keyframes txt1 {
  80% {
    transform: translateX(40px);
  }
  100% {
    transform: translateX(0);
  }
}
div.loader,
.msg_box p {
  transform: translateX(-300px);
  animation: txt2 0.3s forwards;
}
@keyframes txt2 {
  60% {
    transform: translateX(40px);
  }
  80%,
  100% {
    transform: translateX(0);
  }
}

/* msg_box_ready loader */
#msg_box_ready div.loader,
#msg_box_ready div.loader::before,
#msg_box_ready div.loader::after {
  border-radius: 50%;
  width: 1em;
  height: 1em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1.8s infinite ease-in-out;
  animation: load7 1.8s infinite ease-in-out;
}
#msg_box_ready div.loader {
  color: #fff;
  font-size: 10px;
  margin: 0 auto 40px auto;
  position: relative;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
#msg_box_ready div.loader::before,
#msg_box_ready div.loader::after {
  content: "";
  position: absolute;
  top: 0;
}
#msg_box_ready div.loader::before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
#msg_box_ready div.loader::after {
  left: 3.5em;
}
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

#msg_box_result {
  top: 60%;
  height: 250px;
}
#msg_box_result p {
  font-size: 2rem;
}
#msg_box_result img {
  width: 120px;
  height: 120px;
}
#msg_box_result h2 {
  font-size: 4rem;
}

.play_header {
  background: var(--primary);
  flex-shrink: 0;
  width: 300px;
}
.play_header > div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.play_header > div a,
.play_header > div p,
.play_header > div label {
  color: #fff;
  align-items: flex-start;
  font-size: 1.4rem;
  text-align: left;
  flex-grow: 1;
}

.play_header a.logo {
  float: left;
  padding: 0.625em 1.25em;
}
.play_header a.logo img {
  max-width: 106px;
}

input[id*="header_btn"],
input[id*="header_btn"] + .play_header label.header_btn {
  display: none;
}

#my_info > div {
  padding: 0.625em 1.25em;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

#my_info > div > img,
#verify > div > img {
  margin-right: 8px;
  width: 24px;
}

/* adress */
#adress {
  align-items: flex-start !important;
}
#adress .add_network img {
  position: relative;
  top: 2px;
}
#adress button {
  padding: 0.4em 0.8em;
  background: rgba(192, 177, 143, 0.8);
  color: var(--primary);
  font-size: 1.2rem;
}
#adress button:hover {
  background: rgba(192, 177, 143, 1);
}
#adress #connected_wallet {
  display: block;
}
#adress #discord_info {
  color: #fff;
}
#adress #team_color {
  margin-top: 6px;
  margin-right: 8px;
  width: 14px;
  height: 14px;
  border: 1px solid #222;
  border-radius: 50px;
  float: left;
  background-color: #4541f1; /* blue팀일 때 */
  /*background-color: #fff; white팀일 때 */
  /*background-color: #F1D644; gold팀일 때 */
}
#adress #team_name {
  float: left;
}

#btn_connect {
  padding: 0.4em 0.8em;
  background: rgba(192, 177, 143, 0.8);
  color: var(--primary);
  font-size: 1.2rem;
}
/* my_point */
#my_point {
  align-items: flex-start !important;
}
#my_point > a {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
  color: #fff;
  font-size: 1.8rem;
}
#my_point a p span {
  font-size: 1.2rem;
}

/* verify */
#verify > div {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
#verify > div button {
  margin-left: 8px;
  padding: 0.375em 0.625em;
  background: rgba(192, 177, 143, 0.8);
  color: var(--primary);
  font-size: 1.2rem;
}
#verify .not_verify > button:hover {
  background: rgba(192, 177, 143, 1);
  color: var(--primary);
}

/* card_random */
#card_random {
  align-items: flex-start !important;
}
#card_random > div {
  float: left;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
#card_random > div > p {
  margin-top: 8px;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
}

#card_info > img {
  width: 220px;
}
#card_random #card_owner {
  margin-left: 10px;
  padding: 0 6px;
  border-radius: 4px;
  font-family: "SUIT-Regular";
  font-weight: 600;
  font-size: 1.2rem;
  /* master card */
  color: #5a2c99;
  background: #a698b8;

  /* master card */
  /*  color:#5A2C99;
  background:#A698B8;*/
}

#my_info > div label {
  cursor: pointer;
}

#game_warp {
  flex-grow: 1;
  position: relative;
  z-index: 3;
}
.bg-floor {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 300px;
  background: url(../img/bg-floor.jpg);
}
