#game {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: hidden;
  min-height: 100%;
}

/* tv */
#tv {
  position: relative;
  margin: 60px auto 20px auto;
  width: 60%;
  max-width: 850px;
}
#tv::before {
  content: "";
  position: absolute;
  top: 92%;
  left: 50%;
  transform: translateX(-50%);
  width: 130%;
  height: 80%;
  max-height: 400px;
  background: url(../img/table.png) no-repeat;
  background-position: top center;
  background-size: contain;
  z-index: 1;
}
#tv::after {
  content: "";
  position: absolute;
  top: 134%;
  left: 100%;
  transform: translateX(-50%);
  width: 40%;
  height: 44%;
  max-width: 340px;
  max-height: 212px;
  background: url(../img/set-top-box.png) no-repeat;
  background-position: top center;
  background-size: contain;
  z-index: 1;
}

#player_info {
  position: absolute;
  left: 20px;
  top: -42px;
  z-index: 10;
}
#player_info > div {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 6px 15px;
  border-radius: 8px;
  background: var(--BE-Box);
}
#player_info span {
  margin-right: 8px;
  width: 14px;
  height: 14px;
  border-radius: 50px;
}
#player_info #player_info_uesr span {
  background-color: var(--secondary);
}
#player_info #player_info_master span {
  background-color: #5a2c99;
}

#player_info #player_info_master img {
  width: 25px;
  margin-left: 3px;
  margin-right: 3px;
}

#tv .tv_img {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url(../img/TV2.png) no-repeat;
  background-size: contain;
  background-position: bottom center;
  padding: 5.5% 23% 5.5% 5%;
  z-index: 10;
}
#tv .tv_inner {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 20px;
}

#tv .tv_inner > div {
  /*display: flex;
  flex-direction: column;*/
  width: 100%;
  height: 0;
  position: relative;
  left: 0;
  right: 0;
  animation: TVon 0.2s ease-in forwards;
}
@keyframes TVon {
  0% {
    height: 0;
    top: 50%;
  }
  100% {
    height: 50%;
    top: 0;
  }
}

#tv .tv_inner > div.player {
  top: 0;
  background: #d14247;
}
#tv .tv_inner > div.you {
  top: 0;
  background: #51875e;
}
#tv .tv_inner > div::before {
  z-index: 20;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0.3;
}
#tv .tv_inner > div.player::before {
  background: url(../img/man.png) no-repeat;
  background-position: -100% 20%;
  background-size: 25%;
  animation: man 0.3s ease-out forwards;
  animation-delay: 0.8s;
}
@keyframes man {
  0% {
    background-position: -100% 20%;
  }
  70% {
    background-position: 10% 20%;
  }
  100% {
    background-position: 0 20%;
  }
}
#tv .tv_inner > div.you::before {
  background: url(../img/woman.png) no-repeat;
  background-position: 200% 20%;
  background-size: 25%;
  animation: woman 0.3s ease-out forwards;
  animation-delay: 0.6s;
}
@keyframes woman {
  0% {
    background-position: 200% 20%;
  }
  70% {
    background-position: 90% 20%;
  }
  100% {
    background-position: 100% 20%;
  }
}
#tv .tv_inner::after {
  content: "VS";
  font-size: 38px;
  color: var(--point-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 21;
}

#tv .tv_inner h6 {
  position: relative;
  z-index: 21;
  padding: 20px 20px 0 20px;
  font-size: 20px;
  line-height: 24px;
  color: var(--point-color);
}
#tv .tv_inner div.player h6 {
  text-align: left;
}
#tv .tv_inner div.you h6 {
  text-align: right;
}

#tv div div.hand_wrap {
  overflow: auto;
}

#tv div.hand_wrap .hand {
  z-index: 21;
  width: auto;
  height: 76%;
  max-width: 80px;
  max-height: 80px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  animation: hand 0.25s ease-in;
}
@keyframes hand {
  0% {
    transform: translateY(-50%) scale(0, 0);
  }
  100% {
    transform: translateY(-50%) scale(1, 1);
  }
}

#tv div.hand_wrap .hand_left {
  left: 26%;
}
#tv div.hand_wrap .hand_right {
  right: 26%;
}

#tv div.hand_wrap img.select {
  display: block;
  width: auto;
  height: 90%;
  max-width: 110px;
  max-height: 110px;
  transition: all 0.3s;
}

/* game_console */
.game_console_wrap {
  flex-grow: 1;
  width: 100%;
  height: 100%;
  z-index: 20;
  position: relative;
  top: 500px;
  animation: gameconsole 0.3s ease-out forwards;
  animation-delay: 0.7s;
}
@keyframes gameconsole {
  0% {
    top: 500px;
  }
  100% {
    top: 0;
  }
}

.game_console {
  width: 440px;
  height: 250px;
  position: relative;
  margin: 6% auto 50px auto;
  background: url(../img/game-console.png) no-repeat center center / contain;
  display: flex;
  flex-direction: column;
}

/* start btn */
#start > button {
  display: block;
  position: relative;
  height: 40px;
  margin: 30px auto 0 auto;
  width: 100px;
  border: 2px solid #333;
  background-color: #767676;
  box-shadow: 0 6px 0 #555;
  font-size: 2rem;
}
#start > button:active {
  box-shadow: 0 3px 0 #555;
  top: 3px;
}
#start > button:hover {
  font-size: 2.4rem;
}

.game_console_btn_wrap {
  flex-grow: 1;
  position: relative;
  padding: 18px 22px 0 22px;
}
.game_console_btn {
  width: 125px;
  height: 125px;
  border-radius: 100%;
  cursor: pointer;
  border: 2px solid #333;
}
.game_console_btn_wrap button > img {
  width: 78px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: hand 0.2s ease-in;
  transition: all 0.3s;
}
.game_console_btn_wrap button > img:hover {
  width: 86px;
  padding: 12px;
}
.game_console_btn_wrap > .red {
  float: left;
  background: #d14247;
  box-shadow: 0 8px 0 #ab3438;
}
.game_console_btn_wrap > .green {
  float: right;
  background: #51875e;
  box-shadow: 0 8px 0 #4c6752;
}
.game_console_btn_wrap > .red:active {
  top: 6px;
  box-shadow: 0 4px 0 #ab3438;
}
.game_console_btn_wrap > .green:active {
  top: 6px;
  box-shadow: 0 4px 0 #4c6752;
}

/* game_count */
.game_count {
  position: fixed;
  left: 320px;
  bottom: 30px;
  z-index: 10;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px 15px;
  border-radius: 8px;
  background: var(--BE-Box);
}
.game_count > span {
  margin-right: 8px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
}
.game_count img {
  width: 100%;
}

/* points */
.points {
  position: fixed;
  right: 20px;
  bottom: 30px;
  z-index: 10;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px 15px;
  border-radius: 8px;
  background: var(--BE-Box);
}
.points > span {
  margin-right: 8px;
  width: 32px;
  height: 32px;
}

@media screen and (max-width: 1023px) {
  body {
    min-width: 320px;
  }
  .play_content {
    flex-direction: column;
  }

  #countdown {
    position: absolute;
    left: 35%;
    top: 80%;
    z-index: 100;
    padding: 10px;
    margin: 10px;
    display: block;
    color: white;
    font-size: 10rem;
    text-align: center;
  }

  .msg_box {
    left: 0;
  }
  .msg_box h3 {
    font-size: 4rem;
    line-height: 1.2;
  }
  .msg_box h2 {
    font-size: 7rem;
    line-height: 1.2;
  }
  .msg_box p {
    font-size: 2.6rem;
  }
  #msg_box_ready div.loader,
  #msg_box_ready div.loader::before,
  #msg_box_ready div.loader::after {
    width: 0.75em;
    height: 0.75em;
  }
  #msg_box_ready div.loader {
    font-size: 8px;
    margin: 0 auto 30px auto;
  }

  .label_out {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 90;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
    visibility: hidden;
  }
  input[id*="header_btn"]:checked ~ #game_warp .label_out {
    opacity: 1;
    visibility: visible;
  }

  #msg_box_result {
    top: 60%;
    height: 200px;
  }
  #msg_box_result p {
    font-size: 1.6rem;
  }
  #msg_box_result img {
    width: 100px;
    height: 100px;
  }
  #msg_box_result h2 {
    font-size: 3rem;
  }

  .play_header {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 100;
  }
  .play_header > div {
    display: inherit;
    float: left;
    width: 100%;
    padding: 10px 15px;
  }
  .play_header a.logo {
    height: 40px;
    padding: 0;
    float: left;
  }
  .play_header a.logo img {
    width: auto;
    height: 100%;
  }

  input[id*="header_btn"] + .play_header > div > label {
    float: right;
    display: block !important;
    width: 30px;
    height: 30px;
    position: relative;
    margin-top: 6px;
    transition: 0.1s;
  }
  input[id*="header_btn"] + .play_header > div > label span {
    width: 12px;
    height: 12px;
    background-color: var(--point-color);
    display: block;
    border-radius: 2px;
    position: absolute;
  }
  input[id*="header_btn"] + .play_header > div > label:hover span {
    transform: scale(1.1);
    transition: 350ms cubic-bezier(0.8, 0.5, 0.2, 1.4);
  }
  input[id*="header_btn"] + .play_header > div > label span:nth-child(1) {
    left: 0;
    top: 0;
  }
  input[id*="header_btn"] + .play_header > div > label span:nth-child(2) {
    right: 0;
    top: 0;
  }
  input[id*="header_btn"] + .play_header > div > label span:nth-child(3) {
    left: 0;
    bottom: 0;
  }
  input[id*="header_btn"] + .play_header > div > label span:nth-child(4) {
    right: 0;
    bottom: 0;
  }
  input[id*="header_btn"]:checked + .play_header > div > label {
    transform: rotate(180deg);
    cursor: pointer;
    transition: 0.2s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  }
  input[id*="header_btn"]:checked + .play_header > div > label span {
    width: 20px;
    height: 6px;
    transition-delay: 200ms;
    transition: 0.5s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  }
  input[id*="header_btn"]:checked
    + .play_header
    > div
    > label
    span:nth-child(1) {
    top: 6px;
    transform: rotate(45deg);
  }
  input[id*="header_btn"]:checked
    + .play_header
    > div
    > label
    span:nth-child(2) {
    top: 6px;
    transform: rotate(-45deg);
  }
  input[id*="header_btn"]:checked
    + .play_header
    > div
    > label
    span:nth-child(3) {
    bottom: 6px;
    transform: rotate(-45deg);
  }
  input[id*="header_btn"]:checked
    + .play_header
    > div
    > label
    span:nth-child(4) {
    bottom: 6px;
    transform: rotate(45deg);
  }
  #my_info {
    display: none;
  }
  input[id*="header_btn"]:checked + .play_header > div > label + #my_info {
    display: flex;
    flex-direction: column;
    clear: both;
    padding-top: 20px;
  }
  #my_info > div {
    padding-left: 0;
    padding-right: 0;
  }

  #game_warp {
    padding-top: 60px;
  }
  #game_warp::before {
    height: 40%;
  }
  /* tv */
  #tv {
    width: 80%;
    max-width: 600px;
  }
  #tv::before {
    width: 120%;
  }
  #tv .tv_inner h6 {
    padding: 10px 10px 0;
    font-size: 1.6rem;
    line-height: 1.6rem;
  }

  #tv .tv_inner::after {
    font-size: 2.4rem;
  }

  #tv div.hand_wrap .hand_left {
    left: 20%;
  }
  #tv div.hand_wrap .hand_right {
    right: 20%;
  }

  #game {
    justify-content: space-between;
  }

  /* game_console */
  .game_console {
    width: 300px;
    height: 170px;
    margin-top: 20vh;
  }
  #start > button {
    height: 38px;
    width: 76px;
    margin: 20px auto 0 auto;
    font-size: 1.8rem;
  }
  .game_console_btn_wrap {
    padding: 5px 16px 0 16px;
  }
  .game_console_btn {
    width: 84px;
    height: 84px;
  }
  .game_console_btn_wrap button > img {
    width: 52px;
  }

  /* game_count */
  .game_count {
    padding: 4px 10px;
    left: 10px;
    bottom: 20px;
  }
  .game_count > span {
    margin-right: 6px;
    width: 24px;
    height: 24px;
  }
  .game_count img {
    width: 100%;
  }
  .game_count #game_count {
    font-size: 1.4rem;
  }

  /* points */
  .points {
    padding: 4px 10px;
    right: 10px;
    bottom: 20px;
  }
  .points > span {
    font-size: 0.875rem;
  }
}
